.plot-container {
  position: relative;
  margin: 0 0;
  padding: 0 0;
  width: 100%;
  height: 100%;
}

.plot-container * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0 0;
  padding: 0 0;
}

.noselect {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}



